module.exports = ".title {\n  background-color: var(--primary);\n  padding: 10px 8px 10px 8px;\n  margin-bottom: 15px;\n  border-radius: 0 0 12px 12px;\n  box-shadow: 0px -2px 30px 3px rgba(0,0,0,.75);\n}\n.title-logo {\n  width: 35px;\n  display: linear;\n  float: left;\n}\n.title h3 {\n  margin-bottom: 0;\n  font-weight: 700!important;\n  font-size: 22px;\n  line-height: 39px;\n  letter-spacing: 1px;\n  text-transform: uppercase;\n}\nul > li {\n  list-style: none;\n}\nhr {\n  background-color: #303844;\n}\n.username{\n  padding-top: 15px;\n  font-size: 18px;\n  text-align: center;\n  position: relative;\n  font-weight: 100;\n}\n.nav-container{position: relative;}\n.nav{padding-top: 10px;width: 200px;}\n.nav li{width:inherit}\n.nav li a{\n  display: flex;\n  align-items: center;\n\n  color: var(--font-subcolor);\n  \n  height: 50px;\n  text-decoration: none;\n  width: 100%;\n  transition: all 400ms;\n\n}\n.nav li a:hover,  .parent-active{\n  background-color: var(--main-color);\n}\n.nav li a span{\n  margin-left: 20px;\n}\n.selected{\n  color: white!important;\n  border-left: 5px solid #007DF9;\n  transition: none;\n}\n.subMenu {\n  padding: 0;\n  overflow: hidden; \n  background: var(--lighter-color);\n  box-shadow: inset 0 8px 10px -9px rgba(0, 0, 0, 0.42);\n}\n.subMenu-arrow {\n  right: 5px;\n  position: absolute;\n  padding: 15px;\n  transition: -webkit-transform .4s;\n  transition: transform .4s;\n  transition: transform .4s, -webkit-transform .4s\n}\n.subMenu-open {\n  -webkit-transform: rotate(90deg);\n          transform: rotate(90deg);\n}"